/* Sidebar container */
.sidebar {
  width: 25%; /* Fixed width */
  max-width: 350px; /* Optional: Limit maximum width */
  min-width: 300px; /* Optional: Set a minimum width */
  height: 100vh; /* Full height of the viewport */
  background-color: #2c2c2c;
  color: #ffffff;
  padding: 15px;
  border-right: 2px solid #444;
  display: flex;
  flex-direction: column;
  box-sizing: border-box; /* Ensure padding doesn't affect width */
  overflow-y: auto; /* Enable vertical scrolling if content overflows */
}

/* Sidebar heading */
.sidebar h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #e0e0e0;
}

/* Search Bar Styles */
.search-bar {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  background-color: #333;
  padding: 5px;
  border-radius: 8px;
  border: 1px solid #444;
}

.search-bar input {
  flex: 1;
  padding: 8px;
  border: none;
  border-radius: 5px;
  background-color: #333;
  color: #fff;
  outline: none;
}

.search-bar button {
  background-color: #444;
  color: #fff;
  border: none;
  padding: 8px 12px;
  border-radius: 5px;
  margin-left: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.search-bar button:hover {
  background-color: #555;
}

/* Upload Section */
.upload-section {
  margin: 20px 0;
  display: flex;
  justify-content: center;
}

.upload-button {
  margin-top: 50px;
  background-color: #007acc;
  color: #fff;
  padding: 10px 15px;
  border-radius: 5px;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-weight: bold;
}

.upload-button:hover {
  background-color: #005f99;
}

/* Sidebar Menu Styles */
.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
  flex-grow: 1; /* Ensures the menu takes up remaining space */
}

.sidebar li {
  padding: 12px 15px;
  margin-bottom: 10px;
  border-radius: 5px;
  color: #b0b0b0;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.sidebar li:hover {
  background-color: #444;
  color: #ffffff;
}

.sidebar li.active {
  background-color: #007acc;
  color: #ffffff;
}

/* Dropdown Styles */
.dropdown {
  list-style: none;
  padding-left: 20px;
  margin-top: 10px;
}

.dropdown li {
  padding: 8px 15px;
  margin-bottom: 5px;
  background-color: #333;
  border-radius: 5px;
  color: #b0b0b0;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.dropdown li:hover {
  background-color: #444;
  color: #ffffff;
}

.dropdown li.active {
  background-color: #007acc;
  color: #ffffff;
}

/* Extract Bibliography Button */
.extract-bibliography-button {
  background-color: #28a745;
  color: #fff;
  padding: 10px 15px;
  border-radius: 5px;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-weight: bold;
  margin-top: 10px;
}

.extract-bibliography-button:hover {
  background-color: #218838;
}

/* Modal Styles */
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: #2c2c2c;
  color: #fff;
  padding: 20px;
  border-radius: 10px;
  width: 50%;
  max-width: 600px;
  text-align: center;
}

.modal-content h3 {
  margin-bottom: 20px;
}

.modal-content ul {
  list-style: none;
  padding: 0;
  text-align: left;
}

.modal-content li {
  margin-bottom: 10px;
}

.modal-content .close {
  position: absolute;
  top: 10px;
  right: 15px;
  font-size: 1.5em;
  color: #fff;
  cursor: pointer;
}

