.profile-page {
    padding: 20px;
    background-color: #1e1e1e;
    color: #ffffff;
    flex-grow: 1;
    overflow-y: auto;
  }
  
  .profile-header {
    margin-bottom: 20px;
  }
  
  .profile-header h1 {
    color: #e0e0e0;
    margin-bottom: 10px;
  }
  
  .profile-header p {
    margin: 5px 0;
    color: #b0b0b0;
  }
  
  .profile-header p strong {
    color: #ffffff;
  }
  
  .profile-papers h2 {
    margin-top: 20px;
    color: #e0e0e0;
  }
  
  .profile-table {
    width: 100%;
    border-collapse: collapse;
    background-color: #2c2c2c;
    color: #ffffff;
    margin-top: 10px;
  }
  
  .profile-table th,
  .profile-table td {
    padding: 12px 15px;
    border: 1px solid #444;
    text-align: left;
  }
  
  .profile-table th {
    background-color: #333;
    font-weight: bold;
  }
  
  .profile-table tr:hover {
    background-color: #444;
    cursor: pointer;
  }
  