.main-content {
  flex-grow: 1;
  padding: 20px;
  background-color: #1e1e1e;
  color: #ffffff;
  overflow-y: auto; /* Add scrolling if content overflows */
}

.main-content h1 {
  color: #e0e0e0;
  margin-bottom: 20px;
}

/* Table Styles */
.paper-table {
  width: 100%;
  border-collapse: collapse;
  background-color: #2c2c2c;
  color: #ffffff;
}

.paper-table th,
.paper-table td {
  padding: 12px 15px;
  border: 1px solid #444; /* Border between rows and columns */
  text-align: left;
}

.paper-table th {
  background-color: #333;
  color: #ffffff;
  font-weight: bold;
}

.paper-table tr {
  transition: background-color 0.2s ease;
  cursor: pointer; /* Indicate rows are clickable */
}

.paper-table tr:hover {
  background-color: #444; /* Highlight row on hover */
}

.paper-table td {
  color: #b0b0b0;
}

.paper-table td strong {
  color: #ffffff;
}
