.paper-details {
    padding: 20px;
    background-color: #2c2c2c; /* Dark background */
    color: #ffffff; /* White text */
    border-left: 2px solid #444; /* Subtle border for separation */
    height: 100%;
    overflow-y: auto; /* Scrollable if content exceeds height */
    width: 28%; /* Slightly increased width */
    min-width: 320px; /* Ensure minimum width for consistency */
    max-width: 400px; /* Optional: Define maximum width for larger screens */
    box-sizing: border-box; /* Ensure padding doesn't affect width */
}
  
.paper-details h3 {
    color: #ffa500; /* Highlighted title color */
    margin-bottom: 15px;
    font-size: 1.5rem;
}
  
.paper-details p {
    margin: 8px 0;
    color: #b0b0b0; /* Slightly muted text for readability */
    font-size: 1rem;
}
  
.rating-section {
    margin-top: 20px;
    display: flex;
    align-items: center;
    gap: 10px;
}
  
.rating-section strong {
    color: #ffa500;
    font-size: 1.2rem;
}
  
.rating-section .rating-value {
    font-size: 1rem;
    color: #ffa500;
}
  
/* Notes Section */
.notes-section {
    margin-top: 20px;
}
  
.notes-section h4 {
    color: #ffa500; /* Matches the rest of the theme */
    margin-bottom: 10px;
    font-size: 1.2rem;
}
  
.notes-list {
    list-style-type: none;
    padding: 0;
    margin-bottom: 10px;
    max-height: 100px;
    overflow-y: auto; /* Scrollable for long note lists */
}
  
.note-item {
    background-color: #333;
    padding: 8px;
    margin-bottom: 5px;
    border-radius: 5px;
    color: #fff;
    font-size: 0.9rem;
    word-wrap: break-word;
}
  
.note-input {
    width: 100%;
    height: 50px;
    margin-bottom: 10px;
    padding: 10px;
    background-color: #2c2c2c;
    color: #fff;
    border: 1px solid #444;
    border-radius: 5px;
    resize: none;
    font-size: 0.9rem;
}
  
.add-note-button {
    padding: 10px 20px;
    background-color: #ffa500;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    font-size: 0.9rem;
}
  
.add-note-button:hover {
    background-color: #ffcc00;
}
  
.delete-button {
    display: block;
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #ff4d4d;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}
  
.delete-button:hover {
    background-color: #ff1a1a;
}
  
.delete-button:active {
    background-color: #cc0000;
}

.checkbox-section {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.checkbox-section label {
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;
    font-size: 1rem;
    color: #b0b0b0;
    transition: color 0.3s ease;
}

.checkbox-section label:hover {
    color: #ffa500; /* Highlight on hover */
}

.checkbox-section input[type="checkbox"] {
    appearance: none;
    width: 20px;
    height: 20px;
    border: 2px solid #ffa500;
    border-radius: 4px;
    background-color: #2c2c2c;
    cursor: pointer;
    position: relative;
    transition: background-color 0.3s ease, border-color 0.3s ease;
}

.checkbox-section input[type="checkbox"]:checked {
    background-color: #ffa500; /* Checked state */
    border-color: #ffa500;
}

.checkbox-section input[type="checkbox"]::after {
    content: "âœ”";
    font-size: 14px;
    color: #fff;
    position: absolute;
    top: 1px;
    left: 4px;
    display: none; /* Hidden by default */
}

.checkbox-section input[type="checkbox"]:checked::after {
    display: block; /* Show checkmark when checked */
}

.citation-button {
    background-color: #28a745; /* Solid green background */
    color: #ffffff;
    padding: 10px 15px; /* Same padding as Add Note button */
    border: none;
    border-radius: 5px; /* Match Add Note button style */
    font-size: 14px; /* Match font size with Add Note button */
    font-weight: bold;
    text-align: center;
    cursor: pointer;
  }
  
  .citation-button:active {
    background-color: #218838; /* Slightly darker green when pressed */
  }
  
  .citation-button:hover {
    background: linear-gradient(135deg, #218838, #1e7e34); /* Slightly darker gradient on hover */
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.3); /* Enhance shadow on hover */
  }
  
  
  /* Modal Styles */
  .modal {
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal-content {
    background-color: #2c2c2c;
    color: #fff;
    padding: 20px;
    border-radius: 10px;
    width: 50%;
    max-width: 600px;
    text-align: center;
  }
  
  .modal-content h3 {
    margin-bottom: 20px;
  }
  
  .modal-content p {
    text-align: left;
    font-size: 1rem;
  }
  
  .modal-content .close {
    position: absolute;
    top: 10px;
    right: 15px;
    font-size: 1.5em;
    color: #fff;
    cursor: pointer;
  }

  .notes-list {
    list-style-type: none;
    padding: 0;
    margin-bottom: 10px;
    max-height: 200px; /* Adjust height as needed */
    overflow-y: auto; /* Enable scroll for long lists */
  }
  
  .note-item {
    display: flex; /* Use flexbox for alignment */
    justify-content: space-between; /* Space out note text and delete button */
    align-items: center;
    background-color: #333;
    padding: 10px;
    margin-bottom: 5px;
    border-radius: 5px;
    color: #fff;
    font-size: 0.9rem;
    word-wrap: break-word;
  }
  
  .delete-note-button {
    background: none;
    border: none;
    color: #ff4d4d;
    cursor: pointer;
    font-size: 1rem;
    margin-left: 10px;
  }
  
  .delete-note-button:hover {
    color: #ff1a1a;
  }
  
  .review-pdf-button {
    background-color: #6c757d; /* Gray background */
    color: #fff;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    margin-top: 10px;
    transition: background-color 0.3s ease;
  }
  
  .review-pdf-button:hover {
    background-color: #5a6268;
  }
  
  .review-pdf-button:active {
    background-color: #545b62;
  }